.subHome {
  display: grid;
  grid-template-columns: 1.5fr 0.6fr;
  grid-template-rows: 40vh 40vh;
  grid-template-areas:
    "con1 con3"
    "con1 con6";
  grid-gap: 1rem;
  overflow: hidden;
  box-sizing: border-box;
  background-color: #F0F0F0;;
  width: 100%;
  height: 90vh;
  padding-left: 2%;
}
